import { Link } from "react-router-dom";
import Logo from "../images/Logo.png";
import { useState } from "react";
import { Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";
import { useSelector, useDispatch } from "react-redux";
import { logOut } from "../store/actions/auth";
import { BASE_URL } from "../store/constants/url";
import {
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from "reactstrap";

export default function Header() {
  let dispatch = useDispatch();
  const [modal, setModal] = useState(false);
  const toggleModal = () => setModal(!modal);
  let [loginForm, setLoginForm] = useState({});
  let { logined } = useSelector((state) => state.auth);

  const [dropdownOpen, setDropdownOpen] = useState(false);

  const toggle = () => setDropdownOpen((prevState) => !prevState);

  let handleChange = (e) => {
    setLoginForm({
      ...loginForm,
      [e.target.name]: e.target.value,
    });
  };

  let loginHandler = (e) => {
    e.preventDefault();
    let reguest = {
      method: "POST",
      body: JSON.stringify(loginForm),
      headers: {
        "Content-Type": "application/json",
      },
    };

    fetch(`${BASE_URL}/users/sign-in`, reguest)
      .then((r) => r.json())
      .then((data) => {
        if (data.token) {
          localStorage.setItem("x_token", data.token);
          alert("You have logined successfully");
          window.location.href = "/admin/attendance";
        } else {
          alert("Login Error!");
        }
      });
  };

  return (
    <>
      <div className="header">
        <div className="header-content">
        <Link to="/">
          <img className="logo" src={Logo} />
        </Link>
        <div>
          {logined ? (
            <Dropdown isOpen={dropdownOpen} toggle={toggle}>
              <DropdownToggle className="user-menu" caret>username</DropdownToggle>
              <DropdownMenu right>
                <DropdownItem>
                  <Link to="/admin/attendance">Посещаемость</Link>
                </DropdownItem>
                <DropdownItem>
                  <Link to="/admin/report">Отчет</Link>
                </DropdownItem>
                <DropdownItem>
                  <Link to="/admin/groups">Группы</Link>
                </DropdownItem>
                <DropdownItem>
                  <Link to="/admin/children">Воспитанники</Link>
                </DropdownItem>
                <DropdownItem onClick={() => dispatch(logOut())}>
                  Выход
                </DropdownItem>
              </DropdownMenu>
            </Dropdown>
          ) : (
            <button onClick={toggleModal} className="btn btn-login">
              Вход
            </button>
          )}
        </div>
        </div>
      </div>

      <Modal isOpen={modal} toggle={toggleModal}>
        <ModalHeader toggleModal={toggleModal}>Вход в систему</ModalHeader>
        <ModalBody>
          <form onSubmit={loginHandler}>
            <label htmlFor="username" className="form-label">
              Логин
            </label>
            <input
              className="form-control"
              type="text"
              id="username"
              value={loginForm.username}
              name="username"
              onChange={handleChange}
            />
            <label htmlFor="password" className="form-label">
              Пароль
            </label>
            <input
              className="form-control"
              type="password"
              id="password"
              value={loginForm.password}
              name="password"
              onChange={handleChange}
            />
            <div className="end">
              <button
                onClick={toggleModal}
                className="btn btn-add mt-3"
                type="submit"
              >
                Вход
              </button>
              <button
                className="btn btn-secondary ms-3 mt-3"
                onClick={toggleModal}
              >
                Отмена
              </button>
            </div>
          </form>
        </ModalBody>
        <ModalFooter>
          <Link onClick={toggleModal} to="/register">
            Регистрация
          </Link>
        </ModalFooter>
      </Modal>
    </>
  );
}
